.uc-deal{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
    margin-bottom: 15px;
  }
  &-footer{
    @extend %clear;
    padding: 10px 18px 32px;
    .page-action{
      float: right;
      margin: 0;
      padding: 0;
    }
  }
  &-body{
    padding: 0 10px;
  }
  &-tb{
    width: 100%;
    margin-top: 26px;
    thead td,
    thead th{
      height: 32px;
      background: #f5f5f5;
      text-align: center;
      font-weight: normal;
    }
    tbody{
      td{
        padding: 20px 5px;
        text-align: center;
        border-bottom: 1px solid #ddd;
        color: #333;
      }
    }
    .title{
      text-align: left;
      a{
        color: #333;
        &:hover{
          color: $color-orange;
        }
      }
    }
  }
}
