.uc-score{
  @extend %clear;
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
    margin-bottom: 10px;
  }
  &-body{
    padding: 0 18px;
    .filter{
      @extend %clear;
      padding: 18px 15px;
      margin-bottom: 18px;
      background: #ebf8ff;
      .count{
        float: left;
        font-size: 14px;
        em{
          font-size: 16px;
          color: $color-orange;
          font-style: normal;
        }
      }
      .side{
        float: right;
        .date{
          border: 1px solid #ccc;
          width: 102px;
          height: 22px;
          line-height: 22px;
          padding: 0 25px 0 5px;
          background: #fff url(/img/mine/me_home_ic_cal.png) no-repeat 80px center;
        }
        .ubtn{
          padding: 0;
          border: 0;
          display: inline-block;
          margin-left: 10px;
          width: 76px;
          height: 28px;
          line-height: 28px;
          text-align: center;
          background: $color-orange;
          color: #fff;
          border-radius: 2px;
          &:hover{
            background-color: darken($color-orange, 3%);
          }
        }
      }
    }
    .tb{
      width: 100%;
      margin-bottom: 15px;
      thead th,
      thead td{
        height: 30px;
        background: #f5f5f5;
        text-align: center;
        font-weight: normal;
        border-top: 2px solid $color-orange;
      }
      tbody{
        td{
          padding: 18px 5px;
          text-align: center;
          border-bottom: 1px solid #ddd;
        }
      }
      .score{
        font-size: 16px;
        color: $color-orange;
      }
    }
  }
  &-footer{
    @extend .uc-case-footer;
  }
}


.uc-score-doc{
  background: #fff;
  padding: 10px 15px;
  &-title{
    color: #333;
    font-size: 16px;
    font-family: $ff1;
    font-weight: bold;
    margin-bottom: 10px;
  }
  ul{
    padding-left: 20px;
    li {
      list-style-type: square;
    }
    .title{
      font-weight: bold;
      color: #333;
      margin-bottom: 5px;
    }
  }
  p{
    line-height: 1.7;
  }
}
