.uc-order{
  @extend %clear;
  padding: 0 18px;
  margin-bottom: 15px;
  background: #fff;
  &-filter{
    padding-top: 20px;
    margin-bottom: 26px;
    font-size: 16px;
    .w-tab-item{
      padding: 0 10px .2em 5px;
    }
    b{
      margin-left: 10px;
      color: $color-orange;
      font-weight: normal;
    }
  }

  &-header{
    td{
      position: relative;
      height: 32px;
      line-height: 32px;
      padding: 0 10px;
    }
    .date{
      float: left;
      margin-right: 30px;
      color: #333;
    }
    .orderId{
      float: left;
      color: #999;
    }
    .del{
      position: absolute;
      top: 50%;
      right: 10px;
      margin-top: -10px;
      width: 18px;
      height: 20px;
      background: url(/img/mine/me_home_set_ic_del.png) no-repeat center;
      white-space: nowrap;
      text-indent: 100%;
      overflow: hidden;
    }
  }

  &-body{
    td{
      text-align: center;
    }
    .meta{
      color: #999;
      margin-bottom: 3px;
    }
    .u-goods{
      @extend %clear;
      border-top: 1px solid #e6e6e6;
      &:first-child{
        border: 0;
      }
    }
    .u-pic{
      float: left;
      width: 25%;
      min-height: 1px;
      padding: 14px;
      img{
        width: 100%;
        height: auto;
      }
    }
    .u-info{
      float: left;
      width: 50%;
      text-align: left;
      padding: 10px 0;
      .title{
        display: block;
        margin-bottom: 3px;
        color: #333;
        cursor: pointer;
        &:hover{
          color: $color-orange;
        }
      }
    }
    .u-price{
      display: inline-block;
      padding: 14px 10px;
      .price{
        color: #333;
      }
      .num{
        color: #999;
        text-align: right;
      }
    }
    .u-payment,
    .u-consignee{
      color: #333;
    }
    .u-amount{
      .price{
        color: #333;
      }
    }
    .u-status{
      .status{
        display: block;
        margin-bottom: 3px;
        &.disabled{
          color: #999;
        }
      }
      .detail{
        display: block;
        margin-bottom: 3px;
      }
      .cancel{
        display: block;
        margin-bottom: 3px;
        color: $color-orange;
      }
    }
    .u-operate{
      .ubtn{
        display: block;
        margin: 4px auto;
        width: 60px;
        height: 24px;
        line-height: 22px;
        border: 1px solid #ccc;
        border-radius: 2px;
        &-orange{
          color: #fff;
          border-color: $color-orange;
          background: $color-orange;
        }
        &-blue{
          color: #fff;
          border-color: $color-blue;
          background: $color-blue;
        }
      }
      .meta{
        color: #333;
      }
    }
  }

  .page-action{
    float: right;
    padding: 0;
    margin: 0 0 24px;
  }
}

.uc-order2{
  padding-top: 20px;
}


.uc-corder{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
  }
  &-body{
    @extend %clear;
    padding: 0 18px 5px;
    table{
      margin: 5px 0 15px;
      width: 810px;
      border: 1px solid #ddd;
      th{
        height: 30px;
        background: #f5f5f5;
        text-align: center;
        font-weight: normal;
      }
      td{
        padding: 8px;
        border-top: 1px solid #ddd;
        border-right: 1px solid #ddd;
        text-align: center;
      }
      .u-pic{
        padding: 8px 24px;
        img{
          max-width: 100%;
          min-width: 100%;
        }
      }
      .u-title{
        padding: 0 55px;
        text-align: left;
      }
    }
    .urow{
      margin-bottom: 14px;
      .llabel{
        display: inline-block;
        width: 90px;
      }
    }
    .title{
      font-size: 14px;
      font-weight: bold;
      margin-bottom: 16px;
    }
  }
}


.orderreply{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
  }
  &-body{
    padding: 5px 160px 20px 18px;
    table{
      width: 100%;
      border: 1px solid #ddd;
      table-layout: fixed;
      th{
        height: 30px;
        background: #f5f5f5;
        text-align: center;
        font-weight: normal;
      }
      td{
        padding: 8px;
        border-top: 1px solid #ddd;
      }
      .u-pic{
        text-align: right;
        img{
          max-width: 100%;
          max-height: 100%;
        }
      }
      .u-title{
        vertical-align: top;
        .title{
          color: #333;
          margin-bottom: 10px;
        }
        .date{
          color: #999;
        }
      }
      .u-status{
        position: relative;
        text-align: center;
        &:before{
          content: '';
          position: absolute;
          left: 50%;
          bottom: -1px;
          margin-left: -11px;
          border-bottom: 11px solid #ddd;
          border-left: 11px solid transparent;
          border-right: 11px solid transparent;
        }
        &:after{
          content: '';
          position: absolute;
          left: 50%;
          bottom: -1px;
          margin-left: -10px;
          border-bottom: 10px solid #fff;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
        }
        &.noreply:before{
          border-bottom-color: #b8b8b8;
        }
        &.noreply:after{
          border-bottom-color: #f5f5f5;
        }
      }
      .replyed{
        padding: 30px;
        min-height: 100px;
        color: #333;
      }
      .reply{
        padding: 20px;
        border: 1px solid #b8b8b8;
        background: #f5f5f5;
        textarea{
          display: block;
          width: 100%;
          border: 1px solid #b8b8b8;
          padding: 10px;
        }
        .help{
          position: relative;
          height: 30px;
          line-height: 30px;
          color: #999;
          margin-bottom: 4px;
          .crt{
            position: absolute;
            bottom: 40px;
            right: 10px;
            line-height: 1;
          }
        }
        .ubtn{
          display: inline-block;
          width: 110px;
          height: 27px;
          line-height: 25px;
          text-align: center;
          color: #fff;
          background: #fc8c49;
          font-size: 16px;
          font-weight: bold;
          border-radius: 2px;
          border: 1px solid #ff6000;
        }
        label{
          display: inline-block;
          height: 27px;
          line-height: 27px;
          font-weight: normal;
          margin-left: 15px;
          input{
            margin-right: 5px;
            outline: none;
          }
        }
      }
    }
  }
}
