// var
$ff0: Arial, Verdana, "\5b8b\4f53";
$ff1: "microsoft yahei", Helvetica, Arial, sans-serif;

$color-orange: #ff6000;
$color-blue: #0076ff;
$color-red: #ff2626;

$gutter: 15px;

// base
%clear{
  &:before,
  &:after{
    display: table;
    content: '';
  }
  &:after{
    clear: both;
  }
}

@mixin ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

%header{
  @extend %clear;
  position: relative;
  padding: 8px 10px 5px;
  .title{
    float: left;
    color: #333;
    font-size: 16px;
    font-family: $ff1;
    font-weight: bold;
  }
  .side{
    float: right;
  }
}

.table{
  table-layout: fixed;
}


// layout
.uc{
  @extend %clear;
  padding: 30px 0;
  background: #f5f5f5;
  font-family: $ff0;
  font-size: 12px;
  line-height: 1.5;

  &-main{
    float: left;
    padding-left: 140px;
    margin-right: -100%;
    width: 100%;
  }

  &-side{
    float: left;
    width: 140px;
    padding: 0 10px;
  }

  &-main-main{
    float: left;
    padding-right: 230px + 16;
    width: 100%;
  }

  &-main-side{
    float: left;
    margin-left: -230px;
    width: 230px;
  }
}


// widget
.w-tb{
  width: 100%;
  thead th{
    height: 32px;
    line-height: 32px;
    text-align: center;
    font-weight: normal;
    background: #f5f5f5;
  }
  &-gt{
    td{
      height: 17px;
      border: 0 !important;
    }
  }
  tbody td{
    border: 1px solid #e6e6e6;
  }
  tbody tr:first-child td{
    border-bottom-color: #f5f5f5;
    background: #f5f5f5;
  }
}

.w-tab{
  @extend %clear;
  border-bottom: 2px solid #ddd;
  .w-tab-item{
    float: left;
    padding: .2em 8px;
    margin: 0 0 -2px;
    border-bottom: 2px solid transparent;
    cursor: pointer;
    &.active,
    &:hover{
      color: $color-orange;
      // border-bottom-color: $color-orange;
      > a{
        color: $color-orange;
      }
    }
    &.active{
      border-bottom-color: $color-orange;
    }
  }
}

.w-drop{
  position: relative;
  display: inline-block;
  text-align: left;
  &-toggle,
  &-item{
    min-width: 90px;
    display: block;
    padding: 0 10px;
    height: 32px;
    line-height: 32px;
  }
  &-toggle:after{
    content: '';
    position: relative;
    top: -1px;
    display: inline-block;
    width: 8px;
    height: 4px;
    overflow: hidden;
    vertical-align: middle;
    margin-left: 3px;
    background: url(/img/mine/me_ic_darrow_d.png) no-repeat center;
    // transition: transform .1s; // 莫名其妙的bug
  }
  &-menu{
    z-index: 10;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: #fff;
  }
  &:hover{
    background: #fff;
    box-shadow: 0 0 3px rgba(0,0,0,.5);
    &:after{
      z-index: 11;
      content: '';
      position: absolute;
      left: 0;
      bottom: -4px;
      width: 100%;
      height: 8px;
      background: #fff;
    }
    .w-drop-toggle:after{
      transform: rotate(180deg);
    }
    .w-drop-menu{
      display: block;
      box-shadow: 0 0 3px rgba(0,0,0,.5);
    }
  }
}

.w-focus{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
    .side{
      padding-top: 5px;
    }
  }
  &-body{
    padding: 0 0 30px;
    .sld{
      position: relative;
      padding: 0 40px;
      overflow: hidden;
      .wrap{
        overflow: hidden;
      }
      ul{
        position: relative;
        left: 0;
        width: 100%;
        font-size: 0;
        white-space: nowrap;
        // overflow: hidden;
        @extend %clear;
      }
      li{
        display: inline-block;
        width: 25%;
        padding: 8px;
        a{
          position: relative;
          display: block;
          width: 100%;
          padding-top: 100%;
        }
        img{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: auto;
        }
      }
      .prev,
      .next{
        position: absolute;
        top: 50%;
        margin-top: -16px;
        width: 15px;
        height: 31px;
        background: no-repeat center;
        white-space: nowrap;
        text-indent: 100%;
        overflow: hidden;
      }
      .prev{
        left: 16px;
        background-image: url(/img/mine/alb_btn_larrow_nor.png);
        // &:hover{
        //   background-image: url(/img/mine/alb_btn_larrow_hig.png);
        // }
      }
      .next{
        right: 16px;
        background-image: url(/img/mine/alb_btn_rarrow_nor.png);
        // &:hover{
        //   background-image: url(/img/mine/alb_btn_rarrow_hig.png);
        // }
      }
      &:hover{
        .prev{
          background-image: url(/img/mine/alb_btn_larrow_hig.png);
        }
        .next{
          background-image: url(/img/mine/alb_btn_rarrow_hig.png);
        }
      }
    }
  }
}

.w-focus .sld-m{
  li{
    width: percentage(1/5);
  }
}

// step
.w-step{
  @extend %clear;
  position: relative;
  width: 100%;
  margin: 0 auto;
  &:before{
    content: '';
    position: absolute;
    top: 7px;
    left: 0;
    height: 6px;
    width: 100%;
    border-radius: 6px;
    background: #ccc;
  }
  &-item{
    position: relative;
    float: left;
    i{
      position: relative;
      display: block;
      margin: 0 auto;
      width: 20px;
      height: 20px;
      line-height: 20px;
      text-align: center;
      border-radius: 50%;
      color: #fff;
      background: #ccc;
      font-style: normal;
      &.check{
        background: #ccc url(/img/mine/me_home_set_ic_schok.png) no-repeat center;
      }
    }
    span{
      display: block;
      height: 20px;
      line-height: 20px;
      text-align: center;
      color: #999;
    }
    &.on{
      &:before{
        content: '';
        position: absolute;
        top: 7px;
        left: 0;
        width: 100%;
        height: 6px;
        background: #6cd128;
        border-radius: 6px;
      }

      i{
        background-color: #6cd128;
      }

      span{
        color: #6cd128;
      }

      ~ .on:after{
        content: '';
        position: absolute;
        top: 7px;
        left: -3px;
        width: 6px;
        height: 6px;
        background: #6cd128;
      }
    }
  }
}

// global
.uc-menu{
  dl{
    margin-bottom: 25px;
  }
  dt{
    height: 25px;
    font-weight: bold;
    font-size: 14px;
    color: #333;
    font-family: $ff1;
  }
  dd{
    font-size: 12px;
    color: #666;
    height: 22px;
    @include ellipsis;
    &.active a{
      color: $color-orange;
    }
  }
}

.uc-header{
  padding-top: 31px;
  background: #ff6000;
  font-family: $ff0;
  &-body{
    @extend %clear;
    .logo{
      float: left;
      width: 378px;
      height: 88px;
      margin: 0;
      font-size: 16px;
      a{
        display: block;
        width: 100%;
        height: 100%;
        color: #fff;
        background: url(/img/mine/logo.png) no-repeat center;
        white-space: nowrap;
        text-indent: 100%;
        overflow: hidden;
        &:hover{
          background-image: url(/img/mine/logo_hover.png);
        }
      }
    }
    .unav{
      padding: 26px 0 0 60px;
      float: left;
      li{
        float: left;
        position: relative;
        margin-right: 24px;
        > a{
          display: block;
          height: 36px;
          line-height: 36px;
          font-size: 16px;
          vertical-align: bottom;
          padding: 0 14px;
          color: #fff;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .drop {
        z-index: 100;
        position: relative;
      }
      .drop > a{
        &:after{
          content: '';
          position: relative;
          top: -1px;
          display: inline-block;
          width: 8px;
          height: 4px;
          margin-left: 5px;
          vertical-align: middle;
          background: url(/img/mine/me_ic_darrow_d_white.png) no-repeat center;
          transition: transform .1s;
        }
      }
      .drop-menu{
        display: none;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background: #fff;
        padding-bottom: 3px;
        box-shadow: 0 0 3px rgba(0,0,0,.5);
        a {
          display: block;
          width: 100%;
          padding: 6px 14px;
          color: #666;
          &:hover{
            color: $color-orange;
          }
        }
      }
      .drop:hover{
        background: #fff;
        box-shadow: 0 0 3px rgba(0,0,0,.5);
        &:after{
          content: '';
          position: absolute;
          left: 0;
          bottom: -4px;
          width: 100%;
          height: 8px;
          background: #fff;
        }
        .drop-toggle{
          color: #333;
          text-decoration: none;
          &:after{
            background-image: url(/img/mine/me_ic_darrow_d.png);
            transform: rotate(180deg);
          }
        }
        .drop-menu{
          display: block;
        }
      }
    }
    .search{
      position: relative;
      float: right;
      width: 340px;
      padding-top: 16px;
      &-tab{
        float: left;
        height: 24px;
        line-height: 24px;
        padding: 0 10px;
        // background: #f8f8f8;
        color: #fff;
        // &:hover,
        &.active{
          background: #f8f8f8;
          color: $color-orange;
        }
      }
      &-ipt{
        float: left;
        width: 100%;
        font-size: 14px;
        color: #666;
        line-height: 20px;
        padding: 8px 90px 8px 10px;
      }
      &-btn{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 80px;
        border-left: 1px solid #ddd;
        font-size: 14px;
        text-align: center;
        padding: 8px 0;
        color: #333;
        background: #f8f8f8;
      }
    }
  }
}


.uc-load{
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 128px;
  height: 128px;
  background: url(/img/loading.gif) no-repeat center;

  &.show{
    display: block;
  }
}


