.uc-setting{
  background: #fff;
  &-header{
    padding: 18px 26px 10px;
    font-size: 16px;
  }
  .tabCont{
    display: none;
    margin: 0 18px;

    &.active{
      display: block;
    }
  }
  .uinfo{
    position: relative;
    padding-bottom: 60px;
    .errBox{
      visibility: hidden;
      display: block;
      margin: 0 10px 10px;
      height: 36px;
      line-height: 36px;
      padding: 0 10px;
      border-radius: 2px;
      background: #ececec;
    }
    .help{
      line-height: 1.5;
      margin: 5px 0;
    }
    span.error{
      margin-left: 10px;
      color: $color-red;
    }
    &-panel{
      position: absolute;
      top: 42px;
      right: 10px;
      width: 300px;
      height: 130px;
      padding: 28px 15px 0 130px;
      border: 1px solid #dbdbdb;
      background: #f8f8f8;
      .pic{
        position: absolute;
        top: 15px;
        left: 15px;
        width: (130px - 2 - 30);
        height: (130px - 2 - 30);
        border: 2px solid #fff;
        border-radius: 50%;
        img{
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      p{
        @include ellipsis;
        height: 28px;
        margin: 0;
      }
      .lv{
        color: $color-blue;
        img{
          vertical-align: top;
          margin-right: 5px;
        }
      }
    }
    &-row{
      @extend %clear;
      margin-bottom: 28px;
    }
    &-label{
      float: left;
      width: 60px;
      margin-right: -60px;
      padding-right: 3px;
      min-height: 30px;
      line-height: 30px;
      text-align: right;
      font-weight: normal;
      b{
        font-weight: normal;
        color: $color-red;
        font-size: 14px;
        vertical-align: middle;
        margin-right: 3px;
      }
    }
    &-cont{
      float: left;
      padding-left: 60px;
      width: 100%;
      min-height: 30px;
      line-height: 30px;
      b{
        color: #333;
      }
      .edit{
        color: $color-blue;
        margin-left: 15px;
        &:hover{
          text-decoration: underline;
        }
      }
      label.sex{
        margin: 0 20px 0 5px;
      }
    }
    .i-ipt{
      height: 30px;
      line-height: 30px;
      padding: 0 5px;
      border: 1px solid #ccc;
      outline: none;
      &:focus{
        border: 1px solid #2d84ff;
        box-shadow: 0 0 1px #2d84ff;
      }
    }
    .i-select{
      display: inline-block;
      background: transparent;
      vertical-align: middle;
      height: 30px;
      margin-right: 8px;
      border: 1px solid #ccc;
      &.year{
        width: 64px;
      }
      &.month,
      &.day{
        width: 46px;
      }
    }
    .i-radio{
      // outline: none;
    }
    .submit{
      display: block;
      padding: 0;
      width: 110px;
      height: 27px;
      line-height: 25px;
      text-align: center;
      border: 1px solid $color-orange;
      background: #ff8f4c;
      color: #fff;
      border-radius: 2px;
      &:hover{
        background-color: darken(#ff8f4c, 3%);
        border-color: darken($color-orange, 3%);
      }
    }
  }

  .uicon{
    padding: 50px 15px 80px;
    p{
      color: #999;
    }
    .upload{
      display: block;
      width: 180px;
      height: 34px;
      line-height: 32px;
      text-align: center;
      border-radius: 3px;
      border: 1px solid #ccc;
      color: #333;
    }
    .upload-info{
      height: 26px;
      .prog{
        display: none;
        overflow: hidden;
        .wrap{
          display: inline-block;
          margin-top: 6px;
          padding: 5px;
          width: 200px;
          background: #ececec;
        }
        .in{
          width: 0%;
          height: 2px;
          background: #6cd128;
          transition: width .2s;
        }
        .pct{
          margin-left: 10px;
          vertical-align: 2px;
          color: #666;
        }
      }
      .msg{
        display: none;
        margin-left: 5px;
        color: $color-red;
      }
    }
    .preview{
      @extend %clear;
      .view1, .side{
        float: left;
        width: 50%;
      }
      .side{
        position: relative;
        padding: 0 20px;
        p{
          margin-bottom: 50px;
        }
      }
      .view1{
        display: table;
        height: 252px;
        border: 1px solid #dbdbdb;
        .in{
          display: table-cell;
          vertical-align: middle;
          text-align: center;
          height: 252px;
        }
        img{
          max-width: 100%;
          max-height: 100%;
        }
      }
      .view2, .view3{
        display: inline-block;
        width: 102px;
        height: 102px;
        border: 1px solid #dbdbdb;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .view2{
        margin-right: 30px;
        margin-left: 20px;
      }
      .view3{
        width: 50px;
        height: 50px;
      }
    }
    .submit{
      display: block;
      padding: 0;
      margin-top: 20px;
      width: 80px;
      height: 27px;
      line-height: 25px;
      text-align: center;
      border-radius: 2px;
      border: 1px solid $color-orange;
      background: #ff8f4c;
      color: #fff;
      &:hover{
        border-color: darken($color-orange, 3%);
        background-color: darken(#ff8f4c, 3%);
      }
    }
  }
}
