.uc-invoice{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
  }
  &-body{
    padding: 0 18px 18px;
    .title{
      margin: 5px 0 20px;
      height: 31px;
      line-height: 30px;
      border-bottom: 1px solid #ddd;
      text-indent: 8px;
      font-size: 14px;
      color: #333;
    }
    table{
      width: 100%;
      td{
        padding: 0 10px;
        vertical-align: top;
      }
    }
    .help{
      height: 24px;
      font-size: 12px;
      line-height: 1.8;
      color: $color-red;
    }
    .mlabel{
      text-align: right;
      vertical-align: top;
      line-height: 30px;
      b{
        color: $color-red;
      }
    }
    .i-ipt{
      display: block;
      width: 100%;
      height: 30px;
      line-height: 28px;
      border: 1px solid #ccc;
      padding: 0 8px;
      &:focus{
        border-color: $color-blue;
        box-shadow: 0 0 1px $color-blue;
      }
      &.error{
        border-color: $color-red;
        box-shadow: 0 0 1px $color-red;
      }
    }
    .i-select{
      width: 100px;
      height: 30px;
      border: 1px solid #ccc;
      background-color: transparent;
      &:focus{
        border-color: $color-blue;
        box-shadow: 0 0 1px $color-blue;
      }
      &.error{
        border-color: $color-red;
        box-shadow: 0 0 1px $color-red;
      }
    }
    .submit{
      padding: 7px 10px;
      min-width: 100px;
      line-height: 1;
      border: 1px solid $color-orange;
      background: #ff8f4c;
      color: #fff;
      &:hover{
        border-color: darken($color-orange, 3%);
        background-color: darken(#ff8f4c, 3%);
      }
    }
  }
}
