.uc-address{
  background: #fff;
  &-header{
    @extend %header;
  }
  &-body{
    padding: 18px;
    .row-a{
      color: #999;
    }
    .btn-new{
      display: inline-block;
      width: 122px;
      height: 27px;
      line-height: 27px;
      background: $color-orange;
      color: #fff;
      text-align: center;
      border-radius: 2px;
      margin-right: 20px;
    }
    .alist{
      padding-top: 20px;
      .item{
        position: relative;
        min-height: 160px;
        border: 2px solid #ddd;
        padding: 18px;
        margin-bottom: 18px;
        &.active{
          border-color: $color-orange;
          .btn-dft{
            background-color: $color-orange;
            color: #fff;
          }
        }
      }
      .btn-del{
        position: absolute;
        top: 14px;
        right: 14px;
        width: 18px;
        height: 20px;
        overflow: hidden;
        white-space: nowrap;
        text-indent: 100%;
        background: url(/img/mine/me_home_set_ic_del.png) no-repeat center;
      }
      .side{
        position: absolute;
        right: 16px;
        bottom: 16px;
      }
      .btn-dft,
      .btn-edit{
        display: inline-block;
        width: 70px;
        height: 20px;
        line-height: 20px;
        text-align: center;
        color: #333;
        border-radius: 1px;
        &:hover{
          color: $color-orange;
        }
      }
      li{
        padding: 0 0 10px 70px;
      }
      .lab{
        display: inline-block;
        width: 70px;
        margin-left: -70px;
        text-align: right;
        color: #999;
      }
    }
  }
}


.uc-modal{
  font-size: 12px;
  .modal{
    &-dialog{
      width: 690px;
    }
    &-content{
      position: relative;
      border: 4px solid #949494;
      border-radius: 3px;
    }
    &-header{
      padding: 8px 14px;
      background: #f2f2f2;
      font-size: 14px;
    }
    &-close{
      position: absolute;
      top: 5px;
      right: 10px;
      font-family: $ff0;
      font-size: 24px;
      line-height: 1;
      cursor: pointer;
    }
  }
}

.uc-modal-address{
  table{
    width: 100%;
    td{
      padding: 0;
    }
  }
  .mlabel{
    width: 105px;
    padding-right: 5px;
    text-align: right;
    vertical-align: top;
    line-height: 30px;
    b{
      color: $color-red;
    }
  }
  .i-ipt{
    width: 230px;
    height: 30px;
    line-height: 30px;
    padding: 0 8px;
    border: 1px solid #ccc;
    &:focus{
      border-color: $color-blue;
      box-shadow: 0 0 2px $color-blue;
    }
    &.error{
      border-color: $color-red;
      box-shadow: 0 0 2px $color-red;
    }
  }
  .i-select{
    display: inline-block;
    width: 100px;
    background-color: transparent;
    height: 30px;
    border-color: #ccc;
  }
  .help{
    margin: 0 0 5px;
    min-height: 20px;
    line-height: 1.5;
    color: $color-red;
  }
  .submit{
    padding: 7px 10px;
    line-height: 1;
    border: 1px solid $color-orange;
    background: #ff8f4c;
    color: #fff;
    &:hover{
      border-color: darken($color-orange, 3%);
      background-color: darken(#ff8f4c, 3%);
    }
  }
}
