.uc-pwd{
  background: #fff;
  &-header{
    @extend .uc-setting-header;
  }
  &-body{
    padding: 30px 0 46px 200px;
  }
  &-row{
    @extend %clear;
  }
  &-label{
    float: left;
    margin-right: -90px;
    width: 90px;
    line-height: 30px;
    text-align: right;
  }
  &-cont{
    float: left;
    min-height: 30px;
    padding-left: 90px;
    width: 100%;
  }
  .i-ipt{
    display: block;
    width: 212px;
    height: 30px;
    line-height: 28px;
    border: 1px solid #dbdbdb;
    padding: 0 10px;
    background-color: transparent;
    &:focus{
      border-color: $color-blue;
      box-shadow: 0 0 2px $color-blue;
    }
    &.error{
      border-color: $color-red;
      box-shadow: 0 0 2px $color-red;
    }
  }
  .help{
    @include ellipsis;
    // visibility: hidden;
    padding: 5px 80px 5px 0;
    height: 30px;
    line-height: 1.5;
    color: $color-red;
  }
  .lv{
    position: relative;
    display: inline-block;
    width: 216px;
    padding: (30px - 12)/2 0 (30px - 12)/2;
    margin-bottom: 30px;
    &-item{
      float: left;
      height: 12px;
      width: (216px / 3) - 4px;
      margin-right: 4px;
      background: #ccc;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 230px;
      line-height: 30px;
    }
    &.lv-low{
      .low{
        background-color: #ff3e3e;
      }
      &:after{
        content: '弱';
      }
    }
    &.lv-mid{
      .low{
        background-color: #ff3e3e;
      }
      .mid{
        background-color: #ffa21d;
      }
      &:after{
        content: '中';
      }
    }
    &.lv-high{
      .low{
        background-color: #ff3e3e;
      }
      .mid{
        background-color: #ffa21d;
      }
      .high{
        background-color: #42e032;
      }
      &:after{
        content: '强';
      }
    }
  }
  .submit{
    display: block;
    padding: 0;
    width: 212px;
    height: 30px;
    line-height: 28px;
    border: 1px solid $color-orange;
    text-align: center;
    border-radius: 2px;
    color: #fff;
    background: #ff8f4c;
    font-size: 14px;
  }
}

.uc-phone{
  background: #fff;
  &-header{
    @extend .uc-pwd-header;
  }
  &-body{
    height: 350px;
    table{
      width: 100%;
      td{
        vertical-align: top;
        padding: 5px;
        line-height: 30px;
      }
    }
    .help{
      height: 18px;
      margin-top: 3px;
      color: $color-red;
      line-height: 1.5;
    }
    .i-ipt{
      display: block;
      width: 100%;
      height: 30px;
      line-height: 28px;
      border: 1px solid #ccc;
      padding: 0 5px;
      &:focus{
        border: 1px solid #2d84ff;
        box-shadow: 0 0 1px #2d84ff;
      }
    }
    .ubtn{
      display: inline-block;
      width: 118px;
      height: 30px;
      line-height: 28px;
      border: 1px solid #c2c2c2;
      background: #e2e2e2;
      color: #333;
      text-align: center;
      &:hover{
        border-color: darken(#c2c2c2, 3%);
        background: darken(#e2e2e2, 3%);
      }
    }
    .submit{
      display: block;
      padding: 0;
      margin: 0;
      width: 110px;
      height: 27px;
      line-height: 25px;
      text-align: center;
      border: 1px solid $color-orange;
      background: #ff8f4c;
      color: #fff;
      font-weight: bold;
      font-size: 14px;
      border-radius: 2px;
      &:hover{
        border-color: darken($color-orange, 3%);
        background: darken(#ff8f4c, 3%);
      }
    }
  }
  .result{
    display: none;
    text-align: center;
    color: #6cd128;
    font-weight: bold;
    margin-top: 40px;
    .check{
      vertical-align: -3px;
      margin-right: 18px;
    }
  }
}

.w-step-phone{
  width: 370px;
  margin: 40px auto 20px;
  .w-step-item{
    width: 50%;
  }
}
