.uc-comment{
  background: #fff;
  &-header{
    @extend %header;
    padding-top: 10px;
    padding-bottom: 20px;
    .title small{
      color: #999;
      font-size: 12px;
      font-weight: normal;
    }
    .side .ubtn{
      position: relative;
      top: 3px;
      padding: 5px 10px;
      background: $color-orange;
      color: #fff;
    }
  }
  &-body{
    padding: 18px;
    .cmt{
      &-empty{
        text-align: center;
        color: #999;
        padding: 10px 10px 30px;
      }
      &-list{}
      &-title{
        .toggle{
          color: $color-orange;
          margin-left: 10px;
        }
      }
      &-cont{
        @extend .clear;
        padding-top: 10px;
        // margin-top: 10px;
      }
      &-item{
        padding: 20px 0;
        border-bottom: 1px solid #ddd;
        &:first-child{
          border-top: 1px solid #ddd;
        }
        p{
          margin: 5px 0;
        }
        .meta{
          color: #999;
          span{
            margin-right: 30px;
          }
        }
      }
    }
  }
}

.uc-comment2{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
  }
  &-body{
    @extend %clear;
    margin: 0 20px;
    padding-bottom: 20px;
    border-top: 1px solid #ddd;
    .cmt{
      width: 560px;
      padding: 10px 0 0 0;
      margin: 20px 0;
      border: 1px solid #b8b8b8;
      background: #f5f5f5;
      &-help{
        padding-top: 10px;
        color: $color-red;
        li {
          list-style-type: square;
        }
      }
      &-w{
        @extend %clear;
        padding-right: 40px;
      }
      &-u{
        @extend %clear;
        float: left;
        width: 50%;
        padding: 0 10px 10px 90px;
      }
      &-label{
        float: left;
        width: 90px;
        margin-left: -90px;
        padding-right: 5px;
        text-align: right;
        line-height: 22px;
      }
      .i-ipt{
        width: 100%;
        height: 22px;
        line-height: 20px;
        padding: 0 8px;
        border: 1px solid #ccc;
        &.error{
          border-color: $color-red;
          box-shadow: 0 0 1px $color-red;
        }
      }
      &-r{
        width: 100%;
        height: 120px;
        border-top: 1px solid #b8b8b8;
        textarea{
          display: block;
          width: 100%;
          height: 100%;
          border: 0;
          padding: 10px;
          margin: 0;
          &.error{
            outline: 1px solid $color-red;
            box-shadow: inset 0 0 1px $color-red;
            outline-offset: 0;
          }
        }
      }
      &-submit{
        padding: 0;
        width: 110px;
        height: 27px;
        line-height: 25px;
        border: 1px solid $color-orange;
        text-align: center;
        color: #fff;
        background: #ff8f4c;
        &:hover{
          border-color: darken($color-orange, 3%);
          background: darken(#ff8f4c, 3%);
        }
      }
    }
  }
}
