.w-step-auth{
  margin: 20px 50px;
  width: auto;
  .w-step-item{
    width: 25%;
  }
}

.uc-auth{
  background: #fff;
  margin-bottom: 15px;
  padding-bottom: 20px;
  &-header{
    @extend %header;
  }
  &-body{
    padding: 0 20px 20px;
  }
}

// 驳回
.uc-auth-result{
  text-align: center;
  color: #333;
  margin: 10px 0 10px;
  padding: 10px;
  font-size: 14px;
  color: $color-red;
}

// auth step 1
.uc-auth1{
  display: none;
  &.active{
    display: block;
  }
  &-title{
    text-align: center;
    color: #333;
    margin: 10px 0 35px;
    font-size: 14px;
  }
  &-row{
    @extend %clear;
    padding-left: 260px;

    &.type1,
    &.type2,
    &.type3{
      display: none;
    }
    &.show{
      display: block;
    }
  }
  &-label{
    float: left;
    width: 260px;
    margin-left: -260px;
    line-height: 30px;
    text-align: right;
    padding-right: 3px;
    b{
      color: $color-red;
    }
  }
  &-cont{
    float: left;
    width: 100%;
  }

  .help{
    color: $color-red;
    height: 20px;
  }

  .i-ipt{
    display: inline-block;
    width: 260px;
    height: 30px;
    line-height: 28px;
    border: 1px solid #ccc;
    padding: 0 8px;
    &:focus{
      border-color: $color-blue;
      box-shadow: 0 0 2px $color-blue;
    }
    &.error{
      border-color: $color-red;
      box-shadow: 0 0 2px $color-red;
    }
    &.i-ipt-short{
      width: 160px;
    }
  }
  select.i-ipt{
    width: auto;
  }
  label{
    line-height: 30px;
    font-weight: normal;
    margin: 0 10px 0 0;
    input{
      margin-right: 3px;
    }
  }
  &-submit{
    display: block;
    width: 80px;
    height: 27px;
    line-height: 25px;
    text-align: center;
    border: 1px solid $color-orange;
    border-radius: 2px;
    background: #ff8f4c;
    color: #fff;
    font-weight: bold;
    padding: 0;
    &:hover{
      border-color: darken($color-orange, 3%);
      background: darken(#ff8f4c, 3%);
    }
  }
}
// auth step 2
.uc-auth2{
  display: none;
  padding-left: 60px;
  padding-right: 60px;
  &.active{
    display: block;
  }
  &-title{
    @extend .uc-auth1-title;
  }
  &-item{
    padding: 20px 0;
  }
  .upload-btn{
    position: relative;
    display: inline-block;
    width: 122px;
    height: 34px;
    line-height: 34px;
    color: #fff;
    text-align: center;
    border-radius: 2px;
    vertical-align: middle;
    background: #6cd128;
    font-size: 14px;
    border-radius: 2px;
    overflow: hidden;
    &.disabled{
      color: #858585;
      background: #ccc;
    }
  }
  .upload-help{
    margin-left: 30px;
    color: #999;
  }
  .upload-info{
    height: 26px;
    .prog{
      display: none;
      overflow: hidden;
      .wrap{
        display: inline-block;
        margin-top: 6px;
        padding: 5px;
        width: 200px;
        background: #ececec;
      }
      .in{
        width: 0%;
        height: 2px;
        background: #6cd128;
        transition: width .2s;
      }
      .pct{
        margin-left: 10px;
        vertical-align: 2px;
        color: #666;
      }
    }
    .msg{
      display: none;
      margin-left: 5px;
      color: $color-red;
    }
  }
  .preview{
    @extend %clear;
    // margin-top: 26px;
    position: relative;
    .cont{
      float: left;
      display: table;
      width: 170px;
      border: 1px solid #dbdbdb;
      .in{
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        height: 200px;
        width: 170px;
      }
      img{
        max-height: 100%;
        max-width: 100%;
      }
    }
    .exp{
      border: 0;
      margin-left: 150px;
      .in{
        vertical-align: top;
      }
      &-tip{
        position: absolute;
        top: 0;
        left: 250px;
        color: #999;
      }
    }
  }
  &-footer{
    padding-top: 30px;
    text-align: center;
  }
  &-submit,
  &-prev{
    display: inline-block;
    padding: 3px 10px;
    margin-right: 10px;
    min-width: 80px;
    text-align: center;
    border: 1px solid $color-orange;
    background: #ff8f4c;
    color: #fff;
    font-weight: bold;
    &:hover{
      color: #fff;
      background-color: darken($color-orange, 3%);
      background: darken(#ff8f4c, 3%);
    }
  }
}

.uc-auth3{
  &-title{
    @extend .uc-auth1-title;
    .ico{
      margin-right: 20px;
    }
    strong{
      font-size: 16px;
      font-weight: bold;
      color: $color-orange;
      margin-left: 3px;
    }
    a{
      color: $color-blue;
    }
  }
  &-row{
    @extend %clear;
    margin-bottom: 10px;
  }
  &-u{
    float: left;
    width: 50%;
    &:first-child{
      text-align: right;
    }
  }
  &-tb{
    display: table;
    width: 100%;
    margin-top: 30px;
    table-layout: fixed;
  }
  &-c{
    display: table-cell;
    vertical-align: middle;
    width: 33.3333%;
    padding: 0 10px;
    img{
      max-width: 100%;
      height: auto;
    }
  }
}
