.uc-case{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
    .title{
      margin-top: 10px;
    }
    .side{
      a{
        line-height: 34px;
      }
      .update{
        display: inline-block;
        height: 34px;
        width: 110px;
        text-indent: 40px;
        color: #fff;
        font-size: 14px;
        background: url(/img/mine/me_btn_upload_nor.png) no-repeat center;
        margin-right: 15px;
        &:hover{
          background-image: url(/img/mine/me_btn_upload_hig.png);
        }
      }
    }
  }
  &-subheader{
    position: relative;
    padding: 14px 18px 0;
    .filter{
      position: absolute;
      bottom: 8px;
      right: 18px;
      .search{
        float: left;
        input{
          float: left;
          height: 24px;
          padding: 0 8px;
          border: 1px solid #ddd;
        }
        .sbtn{
          float: left;
          width: 41px;
          height: 24px;
          line-height: 24px;
          text-align: center;
          border: 1px solid #ddd;
          border-left: 0;
          background: #f5f5f5;
          color: #666;
          &:hover{
            background: darken(#f5f5f5, 3%);
          }
        }
      }
      select{
        float: left;
        height: 24px;
        margin-left: 14px;
        background-color: transparent;
        border-color: #ccc;
      }
    }
  }
  &-body{
    padding: 0 18px;

    .noitem{
      text-align: center;
      color: #999;
      padding: 50px 0;
    }

    .goods{
      @extend %clear;
      margin-left: -22px;
      .item{
        float: left;
        width: 25%;
        padding: 18px 0 0 22px;
      }
      .pic{
        display: block;
        width: 100%;
        height: 114px;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .title{
        display: block;
        height: 20px;
        line-height: 20px;
        margin: 5px 0 10px;
        @include ellipsis;
      }
      .ft{
        position: relative;
        text-align: center;
        &:after{
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          margin-top: -8px;
          width: 1px;
          height: 14px;
          background: #bbb;
        }
        a{
          color: $color-blue;
          margin: 0 8px;
          &:hover{
            text-decoration: underline;
          }
        }
      }
    }
  }
  &-footer{
    @extend %clear;
    padding: 10px 18px 32px;
    .page-action{
      float: right;
      margin: 0;
      padding: 0;
    }
  }
}
