// user panel
.uc-user{
  height: 144px;
  margin-bottom: $gutter;
  background: url(/img/mine/me_home_bg.png) no-repeat center;
  .u1{
    position: relative;
    float: left;
    height: 100%;
    width: percentage(304/990);
    padding: 35px 10px 0 144px;
    .userIcon{
      position: absolute;
      top: 22px;
      left: 22px;
      width: 100px;
      height: 100px;
      border-radius: 50%;
    }
    .nickName{
      height: 26px;
      font-size: 16px;
      @include ellipsis;
    }
    .meta{
      overflow: hidden;
      margin-bottom: 10px;
      color: #999;
      img{
        vertical-align: top;
      }
      a{
        color: $color-blue;
        margin: 0 20px 0 3px;
        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
  .u2{
    float: left;
    width: percentage(244/990);
    height: 100%;
    overflow: hidden;
    padding: 13px 40px 0;
    .item{
      float: left;
      width: 50%;
      text-align: center;
      padding-bottom: 5px;
      img{
        margin-bottom: 3px;
      }
      b{
        margin-left: 5px;
        color: $color-orange;
      }
    }
  }
  .u3{
    float: left;
    width: percentage((990 - 244 - 304)/990);
    height: 100%;
    padding: 10px 25px;
    .panel{
      position: relative;
      height: 100%;
      padding: 20px 50% 0 20px;
      background: #f5f5f5;
      overflow: hidden;
      p {
        margin-bottom: 0;
        height: 24px;
      }
      b{
        color: #333;
      }
      .sign{
        position: absolute;
        left: 50%;
        top: 50%;
        margin-top: -28px;
        height: 48px;
        width: 108px;
        overflow: hidden;
        white-space: nowrap;
        text-indent: 100%;
        background: url(/img/mine/me_home_btn_signin_nor.png) no-repeat center;
        &.disabled{
          background-image: url(/img/mine/me_home_btn_signin_sel.png);
        }
      }
    }
  }
}


.uc-order0{
  margin-bottom: 15px;
  &-header{
    @extend %header;
    margin-bottom: 8px;
    .side{
      padding-top: 2px;
      a{
        color: #666;
        &:hover{
          color: $color-orange;
        }
      }
    }
  }
  .uc-order-body .u-pic{
    width: 33.3333%;
  }
  .uc-order-body .u-info{
    width: 66.6666%;
  }
}

.uc-hot{
  &-header{
    .title{
      padding: 4px 15px;
      color: #fff;
      background: #ff9700;
      font-size: 16px;
      font-family: $ff1;
      small{
        font-size: 12px;
        margin-left: 12px;
      }
    }
  }
  &-body{
    padding: 8px;
    background: #fff;
  }
  &-item{
    margin-bottom: 10px;
    .pic{
      display: block;
      margin-bottom: 5px;
      img{
        display: block;
        width: 100%;
        height: auto;
      }
    }
    .title{
      display: block;
      margin-bottom: 3px;
    }
    .price{
      display: block;
      font-size: 16px;
      color: $color-orange;
    }
  }
}
