.uc-focus{
  background: #fff;
  &-header{
    position: relative;
    padding: 14px 12px 5px;
    .title{
      margin: 0;
      color: #333;
      font-family: $ff1;
    }
    .side{
      position: absolute;
      top: 10px;
      right: 14px;
      .page-action{
        margin: 0;
      }
    }
  }
  &-body{
    .noitem{
      text-align: center;
      color: #999;
      padding: 50px 0;
    }

    // padding: 0 18px;
    .operate{
      padding: 3px 14px;
      label{
        margin-right: 25px;
        user-select: none;
        color: #666;
      }
      input{
        margin-right: 5px;
        outline: none;
      }
      a{
        color: #0076ff;
        &:hover{
          text-decoration: underline;
        }
      }
    }
    .goods{
      @extend %clear;
      // margin: -16px 16px 0 -16px;
      margin: -16px 0 0 -16px;
      padding-right: 16px;
      .item{
        float: left;
        width: 25%;
        // padding: 0 14px 18px;
        padding: 18px 0 0 32px;
      }
      .pic{
        position: relative;
        display: block;
        width: 100%;
        padding-top: 100%;
        overflow: hidden;
        img{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: auto;
        }
        margin-bottom: 5px;
      }
      .title{
        @include ellipsis;
        height: 20px;
        display: block;
        color: #666;
        &:hover{
          text-decoration: underline;
          color: $color-orange;
        }
        input{
          outline: none;
          margin-right: 8px;
        }
      }
      .price{
        height: 20px;
        margin: 8px 0;
        font-size: 14px;
        font-weight: bold;
        color: $color-orange;
      }
      .date{
        @include ellipsis;
        margin: 0;
        padding-bottom: 3px;
        color: #999;
      }
      .ft{
        @extend .clear;
        a{
          float: left;
          width: 48%;
          text-align: center;
          border: 1px solid #ddd;
          border-radius: 2px;
          padding: 4px 8px;
          color: #666;
          font-size: 13px;
          &:first-child{
            margin-right: 3%;
          }
          &:hover{
            color: #666;
            background: darken(#fff, 3%);
          }
        }
      }
    }
  }
  &-footer{
    @extend %clear;
    padding: 24px 18px 32px;
    .pageJump{
      float: right;
      margin-left: 40px;
    }
    .page-action{
      margin: 0;
      padding: 0;
      float: right;
    }
  }
}


