.uc-grow{
  background: #fff;
  margin-bottom: 15px;
  padding-bottom: 22px;
  &-header{
    @extend %header;
  }
  &-body{
    padding: 0 18px;
    @extend %clear;
    .panel{
      position: relative;
      height: 132px;
      // border-top: 2px solid $color-orange;
      // background: #ffe7d6;
      padding-top: 2px;
      background: url(/img/mine/me_home_myji_bg.png) center top;
      .pic{
        position: absolute;
        left: 15px;
        top: 15px;
        width: (130px - 30);
        height: (130px - 30);
        border-radius: 50%;
        border: 2px solid #fff;
        // overflow: hidden;
        .userIcon{
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
        .lv{
          position: absolute;
          top: 0;
          right: 0;
        }
      }
      .unit{
        position: absolute;
        top: 20px;
        left: 167px;
        width: 120px;
        span{
          display: block;
          height: 28px;
          color: #333;
          @include ellipsis;
        }
        .ubtn{
          display: block;
          width: 90px;
          height: 28px;
          line-height: 28px;
          color: #fff;
          background: $color-orange;
          text-align: center;
          border-radius: 2px;
          &:hover{
            background: darken($color-orange, 3%);
          }
        }
      }
      .meta{
        padding: 34px 0 0 295px;
        .crt{
          color: #333;
          margin-right: 20px;
          line-height: 30px;
        }
        strong{
          margin: 0 3px;
          font-size: 16px;
          color: $color-orange;
        }
        em{
          font-size: 16px;
          color: #333;
          font-weight: normal;
          font-style: normal;
        }
        img{
          vertical-align: -2px;
          margin: 0 2 px;
        }
      }
      .lvline{
        position: relative;
        margin: 24px 0 0 167px;
        ul{
          @extend %clear;
        }
        li{
          z-index: 1;
          position: relative;
          float: left;
          width: percentage(1/6);
        }
        .line{
          z-index: 0;
          position: absolute;
          top: 50%;
          left: 15px;
          margin-top: -2px;
          width: percentage(5/6);
          height: 4px;
          background: #e6bea5;
          overflow: hidden;
          .point{
            display: block;
            height: 100%;
            width: 0;
            background: $color-orange;
          }
        }
      }
    }
    .panel2{
      .title{
        font-size: 14px;
        color: #333;
        margin-bottom: 15px;
      }
      p{
        line-height: 2;
        padding: 0 5px;
      }
      strong{
        color: $color-orange;
      }
      img{
        display: block;
        width: 100%;
        height: auto;
        margin-bottom: 25px;
      }
    }
    .panel3{
      .title{
        font-size: 14px;
        color: #333;
        margin-bottom: 15px;
      }
      .cont{
        margin: 0 5px;
        padding: 15px 10px;
        border: 1px solid #e6e6e6;
        border-top: 2px solid $color-orange;
        background: #ebf8ff;
      }
      p{
        line-height: 1.8;
      }
      .expression{
        margin: 30px 0;
        .lbtn,
        .rbtn{
          display: inline-block;
          padding: 8px 20px;
          background: #fff;
          border: 1px solid #e5e5e5;
          color: $color-orange;
          font-size: 14px;
        }
        .rbtn{
          color: $color-blue;
        }
        span{
          display: inline-block;
          padding: 8px 20px;
          font-size: 14px;
        }
      }
      em{
        font-style: normal;
        color: $color-orange;
      }
    }
  }
}
