.uc-coupon{
  background: #fff;
  margin-bottom: 15px;
  &-header{
    @extend %header;
  }
  &-body{
    padding: 0 18px;
    .filter{
      height: 32px;
      line-height: 32px;
      background: #f5f5f5;
      padding: 0 37px;
      margin-bottom: 15px;
      a{
        margin-right: 20px;
        color: #666;
        &:hover,
        &.active{
          color: $color-orange;
        }
      }
    }
    .coupons{
      @extend %clear;
      .item{
        @extend %clear;
        position: relative;
        height: 148px;
        padding: 0 17px;
        margin-bottom: 15px;
        font-family: $ff1;
        background: no-repeat left center;
        &:after{
          display: none;
          content: '';
          z-index: 10;
          position: absolute;
          top: 0;
          right: 0;
          width: 75px;
          height: 75px;
          background: no-repeat center;
        }
        .card{
          position: relative;
          float: left;
          width: percentage(240/710);
          height: 100%;
          margin-right: percentage(-240/710);
        }
        .info {
          float: left;
          width: 100%;
          padding: 15px 20px 15px percentage((240+14)/710);
        }
        .date{
          position: absolute;
          left: 0;
          bottom: 26px;
          width: 100%;
        }
        .price{
          padding-top: 16px;
          line-height: 1;
          vertical-align: bottom;
          color: #fff;
          font-size: 38px;
          font-weight: bold;
          margin-bottom: 3px;
          &:first-letter{
            vertical-align: 10px;
            margin-right: 8px;
            font-family: $ff0;
            font-size: 16px;
            font-weight: normal;
          }
        }
      }
      .item-1{
        background-image: url(/img/mine/me_yhq_bg1.png);
        .card{
        color: #fff;
          &:after{
            content: '蓝券';
            position: absolute;
            top: 14px;
            right: 18px;
            line-height: 1;
            font-family: $ff1;
            font-size: 30px;
            color: #fff;
            opacity: .5;
          }
        }
      }
      .item-2{
        background-image: url(/img/mine/me_yhq_bg2.png);
        .card{
          color: #fff;
          &:after{
            content: '橙券';
            position: absolute;
            top: 14px;
            right: 18px;
            line-height: 1;
            font-family: $ff1;
            font-size: 30px;
            color: #fff;
            opacity: .5;
          }
        }
      }
      .item-used:after{
        display: block;
        background-image: url(/img/mine/me_yhq_ic_sy.png);
      }
      .item-over{
        background-image: url(/img/mine/me_yhq_bg3.png);
        &:after{
          display: block;
          background-image: url(/img/mine/me_yhq_ic_gq.png);
        }
      }
      .item-freeze{
        background-image: url(/img/mine/me_yhq_bg3.png);
        &:after{
          display: block;
          background-image: url(/img/mine/me_yhq_ic_dj.png);
        }
      }
    }
  }
}
